.Navbar {
  width: 100%;
  height: 80px;
  background-color: rgb(53, 61, 47);
}
.navbarWrapper {
  height: 100%;
  padding: 0px 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  font-weight: 200;
  font-size: 26px;
  color: #e6fafc;
  margin-left: 40px;
}
.logo:hover {
  color: #6ba368;
  text-decoration: underline;
  cursor: pointer;
  line-height: 39px;
}

.navMenu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
  font-weight: 200;
  font-size: 16px;
  cursor: pointer;
}

.navItem {
  display: flex;
  align-items: center;
  height: 80px;
  margin-right: 20px;
  color: #e6fafc;
  text-transform: capitalize;
  text-decoration: none;
  list-style: none;
}
.link {
  text-decoration: none;
  color: inherit;
}
.link:hover {
  color: #6ba368;
  transition: background 0.3s ease-in-out;
}
.bars {
  color: #e6fafc;
}

.navLinksMobile {
  display: none;
}
.navMenuBtn {
  display: none;
}

@media screen and (max-width: 960px) {
  .Navbar {
    position: relative;
  }

  .navMenu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    justify-content: flex-start;
  }

  .navMenu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .navLinks {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .navLinks:hover {
    background-color: #1888ff;
    border-radius: 0;
  }

  .logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .navMenuBtn {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #1888ff;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .navLinksMobile:hover {
    background: #fff;
    color: #1888ff;
    transition: 250ms;
  }
}
