.article {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.articleTitle {
  font-weight: bold;
  font-size: 20px;
  text-decoration: underline;
  margin-bottom: 20px;
  text-transform: uppercase;
}
.articleContainer {
  display: flex;
  align-items: center;
  width: 60%;
  justify-content: center;
  flex-direction: column;
  border: 1px solid black;
}

.draya {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 0 60%;
  margin-bottom: 20px;
}
.articleContent {
  margin: 10px 20px;
}
button.readmore {
  background-color: transparent;
  color: #9cfc97;
  font-weight: bolder;
  font-size: 12px;
  text-decoration: underline;
}
button.next {
  margin: 10px 0px;
  cursor: pointer;
  width: 70px;
  text-transform: capitalize;
  border: none;
  background-color: #9cfc97;
}
button.next:hover {
  color: white;
  background-color: #6ba368;
}
