.c {
  height: 100vh;
  background-color: #353d2f;
}

.c-wrapper {
  padding: 80px;
  display: flex;
}

.c-left {
  flex: 1;
}

.c-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px 10px;
}

.c-title {
  font-size: 40px;
  width: 60%;
  color: #fff;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin: 30px 0px;
  font-weight: 200;
  width: 50%;
  color: #fff;
}

.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.c-desc {
  font-weight: 150;
  color: #fff;
}

form {
  margin-top: 20px;
}

input {
  width: 50%;
  height: 50px;
  border: none;
  border-bottom: 1px solid white;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}

textarea {
  width: 100%;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}

button.con {
  border: white;
  width: 120px;
  height: 50px;
  padding: 15px;
  background-color: #353d2f;
  color: #fff;
  font-weight: 300;
  font-size: 16px;
  text-transform: uppercase;
  cursor: pointer;
}
button:hover {
  background-color: #6ba368;
}

@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }

  .c-title {
    font-size: 30px;
  }

  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }

  .c-desc {
    display: none;
  }

  form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }

  input {
    width: 35%;
    height: 40px;
    margin: 10px;
    margin-left: 0;
  }

  button {
    margin-top: 10px;
  }
}
.social {
  display: flex;
  list-style: none;
  justify-content: flex-start;
}
.foot {
  margin: 20px 550px;
}
